import { getData } from '../../Services/getData'
import { PRODUCTS_URL } from '../../constants/url'

import burger from '/src/assets/img/burgers/burger-1.jpg'

class Products {
  async render() {
    const data = await getData.render(PRODUCTS_URL)
    console.log('data', data)

    let productElement = ''

    data.forEach(({ image, price, title, weight, category }) => {
      if (category !== 'burger') {
        return
      }

	  console.log(image)
      productElement += `
				<li class="product">
					<div class="product__img-box">
						<img src="${burger}" class="product__img">
					</div>
					<div class="product__info">
						<span class="product__price">${price}₽</span>
						<h4 class="product__descr fourth-title">${title}</h4>
						<span class="product__weight">${weight}</span>
					</div>
				</li>
			`
    })

    const productWrapper = `<ul class="product__container">${productElement}</ul>`

    return productWrapper
  }
}

export default new Products()
